import { Component, Input, Renderer2, ViewChild } from "@angular/core";
import {
  Animation,
  AnimationController,
  IonSlides,
  NavController
} from "@ionic/angular";
import { CategoriesModel } from "src/app/models/categories.model";
import { PageTransitionService } from "src/app/services/page-transition.service";
import { RecipesModel } from "src/app/models/recipes.model";

import { discoverRecipeCardSlideAnimation, discoverRecipeCardSlideAnimationDuration } from 'src/app/animations/discover-recipe-card-slide.animation';
import { AuthService } from 'src/app/services/auth.service';
import { DataV2Service } from 'src/app/services/data-v2.service';

let activeIndex: number = 0;

@Component({
  selector: "app-home",
  templateUrl: "home.page.html",
  styleUrls: ["home.page.scss"]
})
export class HomePage {
  anim: Animation;
  isSliding: any = false;
  willSlideLeft: any = false;
  willSlideRight: any = false;

  @ViewChild("slides", { read: IonSlides }) slides: IonSlides;
  @ViewChild("expandableRecipe") expandableRecipe;
  @ViewChild("activeImage") activeImage;
  @ViewChild("activeContent") activeContent;

  categories: any[] = [
    // { id: 1, name: "category 1", thumbnail: "/assets/images/category-1.png" },
    // { id: 2, name: "category 2", thumbnail: "/assets/images/category-1.png" },
    // { id: 3, name: "category 3", thumbnail: "/assets/images/category-1.png" }
  ];
  recipes: any[] = [];
  featuredRecipes: any[] = [];
  lastRecipes: any[] = [];
  activeRecipe: any = {
    id: 1,
    name: "Chinese Dumplings",
    description: "Chinese dumplings recipe description",
    cookingTime: { hour: 1, min: 10 },
    thumbnail: "/assets/images/dish-1.png",
    dificulty: "easy"
  };
  slideOpts = {};


  constructor(
    public navCtrl: NavController,
    public broadcast: PageTransitionService,
    public recipeModel: RecipesModel,
    public categoryModel: CategoriesModel,
    public animation: AnimationController, public auth: AuthService, public renderer: Renderer2,
    public dataService: DataV2Service
  ) {
    this.slideOpts = {
      initialSlide: 0,
      speed: 400,
      loop: true,
      on: {
        slideChange() {
          if (this.activeIndex !== activeIndex) {
            if (activeIndex > this.activeIndex) {
              activeIndex = this.activeIndex;
              const btn = document.getElementById(
                "recipe-slider-transition-right-button"
              );
              if (btn) btn.click();
            } else {
              activeIndex = this.activeIndex;
              const btn = document.getElementById(
                "recipe-slider-transition-left-button"
              );
              if (btn) btn.click();
            }
          }
        }
      }
    };

    this.loadRecipes();
  }

  @Input('header') header: any;
  scrollStart(header) {
    this.header = header.el
  }

  lastX: any;
  logScroll(event) {

    if (event.detail.scrollTop > Math.max(0, this.lastX)) {
      // Hide
      this.renderer.setStyle(this.header, 'margin-top', `-${this.header.clientHeight}px`);
      this.renderer.setStyle(this.header, 'transition', 'margin-top 400ms');
    } else {
      // Show
      this.renderer.setStyle(this.header, 'margin-top', '0px')
      this.renderer.setStyle(this.header, 'transition', 'margin-top 400ms');
    }

    this.lastX = event.detail.scrollTop
  }

  ngAfterViewInit() { }

  async loadRecipes() {

    const _recipes: any = await this.dataService.getRecipes();
    const _categories: any = await this.dataService.getCategories();

    console.log({ _recipes, _categories });
    const recipes: any = await this.recipeModel.get({ limit: 10 });
    // const categories: any = await this.categoryModel.get();
    /*
    this.recipes = recipes.slice(3, recipes.length);

    this.featuredRecipes = recipes.slice(0, 3).map(el => {
      el.featuredImage = "/assets/images/dish-2.png";
      return el;
    });


    this.categories = _categories.map(el => {
      el.thumbnail = "/assets/images/category-1.png";
      return el;
    });
    */
    this.recipes = _recipes;
    this.featuredRecipes = recipes.slice(0, 3).map(el => {
      el.featuredImage = "/assets/images/dish-2.png";
      return el;
    });
    this.categories = _categories;
    /*
    this.categories = _categories.map(el => {
      if (!el.thumbnail) {
        el.thumbnail = "/assets/images/category-1.png";

      }
      return el;
    });
    */
    this.activeRecipe = this.featuredRecipes[0];

  }

  transitionRight() {
    this.slideTransition();
  }

  transitionLeft() {
    this.slideTransition();
  }

  slideTransition() {
    discoverRecipeCardSlideAnimation(this.activeImage.nativeElement, { side: "right" })
    discoverRecipeCardSlideAnimation(this.activeContent.nativeElement, { side: "left" })
    setTimeout(() => {
      this.activeRecipe = this.featuredRecipes[activeIndex];
    }, discoverRecipeCardSlideAnimationDuration)

  }

  goToRecipe(activeRecipe, event: any = false) {
    this.broadcast.emit(
      this.broadcast.getDomInformation(
        {
          container: this.expandableRecipe.nativeElement,
          activeImage: this.activeImage.nativeElement
        },
        event
      )
    );
    setTimeout(() => {
      let navigationExtras = {
        queryParams: {
          recipe: JSON.stringify(activeRecipe)
        },
        animated: false
      };
      this.navCtrl.navigateForward(`recipe/${activeRecipe.id}`, navigationExtras);
    }, 300);
  }
}
